import { TextItemConfig } from "../items/Text"
import { ImageItemConfig } from "../items/Image"
import { createItemElement } from "../items/Item"

type ItemConfigs = TextItemConfig | ImageItemConfig

export interface ViewConfig {
	items: ItemConfigs[]
	duration?: number // TODO if duration is set dont care about animation done
	backgroundColor?: string
}

// This and all its properties are som stupidly named
export class DoneManager {
	private num: number
	private counted: number
	private done: () => void

	constructor (num: number, done: () => void) {
		this.num = num
		this.done = done
		this.counted = 0
	}

	public count() {
		this.counted = this.counted + 1

		if (this.counted == this.num)
			this.done()
	}
}

export function createView(vconf: ViewConfig, viewDone: () => void) {
	const viewDiv = document.createElement("div")

	const viewDoneMgr = new DoneManager(vconf.items.length, viewDone)
	const haveAnimation = vconf.items.some(i => i.animation)

	vconf.items.forEach(iconf => {
		const element = createItemElement(iconf, () => {
			if (haveAnimation)
				viewDoneMgr.count()
		})

		viewDiv.appendChild(element)
	})

	// If a duration is set it should overrule animations being done
	if (vconf.duration || !haveAnimation)
		setTimeout(() => {
			viewDone()
		}, (vconf.duration ?? 3) * 1000)

	return viewDiv
}
