import { createImageItem, ImageItemConfig } from "./Image"
import { createTextItem, TextItemConfig } from "./Text"

export type ItemConfig = ImageItemConfig | TextItemConfig

export interface ItemBaseConfig {
	type: string
	animation?: ItemAnimation
	css?: Partial<CSSStyleDeclaration>
	className?: string
}

export interface ItemAnimation {
	keyframes: Keyframe[] | PropertyIndexedKeyframes
	duration?: number // the duration for the animation
	delay?: number // delay until the animation starts
	stay?: number // time after delay and duration to mark element as done
}

function setUpAnimation(element: HTMLElement, animation: Required<ItemAnimation>, done: () => void) {
	const duration = 1000 * animation.duration
	const keyframes = animation.keyframes
	const delay = 1000 * animation.delay
	const stay = 1000 * animation.stay

	setTimeout(() => {
		element.animate(keyframes, {duration: duration, fill: "forwards"})

		setTimeout(() => {
			done()
		}, duration+stay)
	}, delay)
}

export function createItemElement(iconf: ItemConfig, itemDone: () => void): HTMLElement {
	let itemElement: HTMLElement

	if (iconf.type == "text")
		itemElement = createTextItem(iconf)
	else if (iconf.type == "image")
		itemElement = createImageItem(iconf)
	else {
		console.warn("Unknown item type")
		// TODO return null
		return document.createElement("div")
	}

	if (iconf.animation) {
		setUpAnimation(itemElement, {
			duration: 1,
			delay: 1,
			stay: 1,
			...iconf.animation
		}, () => itemDone())
	}
	else {
		itemDone()
	}

	return itemElement
}
