import { ItemBaseConfig } from "./Item";

export interface ImageItemConfig extends ItemBaseConfig {
	type: "image",
	src: string,
	scale?: number,
}

export function createImageItem(iconf: ImageItemConfig): HTMLElement {
	const img = document.createElement("img")

	img.src = iconf.src

	const style = {
		position: "absolute",
		transform: "",
		height: iconf.scale ? "calc(" + (iconf.scale * 100) + "%)" : "100%",
		...iconf.css,
	}

	Object.assign(img.style, style)

	if (iconf.className)
		img.className = iconf.className

	return img
}
