import { ItemBaseConfig } from "./Item"

type TextEffects = "typewriter"

type TextType = string | TextConfig | TextType[]

interface TextConfig {
	content: string,
	css?: Partial<CSSStyleDeclaration>
	className?: string
}

type TextHeadingType = "h1" | "h2" | "h3"

export interface TextItemConfig extends ItemBaseConfig {
	type: "text",
	tag?: TextHeadingType

	text: TextType

	size?: string

	effect?: TextEffects
}

function createTextFragments(text: TextType, maxLength?: number): HTMLElement[] {
	var length = 0

	const shorten = (s: string) => {
		const o = !maxLength ? s : s.slice(0, maxLength - length)
		length += o.length
		return o.replace(/\s$/, "\u00a0") // spaces not visible otherwise at the end of span
	}

	const parseString = (s: string) => {
		let short = shorten(s)

		let elem = document.createElement("span")
		elem.innerText = short

		return elem
	}

	const parseTextConfig = (t: TextConfig) => {
		let content = t.content
		let short = shorten(content)

		let elem = document.createElement("span")
		elem.innerText = short
		Object.assign(elem.style, t.css ?? {})

		if (t.className)
			elem.className = t.className

		return elem
	}
	
	const parseTextTypes = (tt: TextType): HTMLElement[] => {
		if (typeof tt === "string") {
			const text = tt as string
			return [parseString(text)]
		}
		else if (Array.isArray(tt)) {
			return tt.map(t => {
				return parseTextTypes(t)
			}).flat()
		}
		else {
			return [parseTextConfig(tt)]
		}
	}

	var elements: HTMLElement[] = parseTextTypes(text)

	return elements
}

export function createTextItem(iconf: TextItemConfig) {
	const textElement = document.createElement(iconf.tag ?? "h1")

	Object.assign(textElement.style, {
		position: "absolute",
		transform: "translate(-50%, 0)",
		fontSize: iconf.size,
		display: "flex",
		flexWrap: "wrap",
		...iconf.css,
	})

	if (iconf.effect === "typewriter") {
		const fullLength = createTextFragments(iconf.text).reduce((acc, c) => acc + c.innerText.length, 0)

		var i = 1
		const typeDelay = 100
		const twInterval = setInterval(() => {
			if (i > fullLength) {
				clearInterval(twInterval)
				return
			}

			const textFragments = createTextFragments(iconf.text, i++)
			textElement.replaceChildren(...textFragments)
		}, typeDelay)
	}
	else {
		const textFragments = createTextFragments(iconf.text)
		textFragments.forEach(f => {
			textElement.appendChild(f)
		})
	}

	return textElement
}
