import { createView, ViewConfig } from "./View"
import { BLI_MEDLEM } from "./ssu_views/bli_medlem"
import { FOLJ_INSTA } from "./ssu_views/folj_insta"
import { GA_MED_I_S } from "./ssu_views/ga_med_i_s"
import { MOTE_ONSDAG } from "./ssu_views/mote_onsdag"
import { SSU_RED } from "./ssu_views/common"
import { KOLLEKTIVTRAFIK } from "./ssu_views/kollektivtrafik"

export function createViewManager() {
	let div = document.createElement("div")

	const style = {
		overflow: "hidden",
		position: "absolute",
		width: "1920px",
		height: "1080px"
	}

	Object.assign(div.style, style)

	var viewIndex = 0
	const nextIndex = () => {
		viewIndex = ++viewIndex % MY_CONFIG.length
		setIndex(viewIndex)
	}

	const setIndex = (i: number, frozen: boolean = false) => {
		const vconf = {
			...MY_CONFIG[i]
		}

		const view = createView(vconf, !frozen ? nextIndex : () => {})
		div.replaceChildren(view)
		div.style.backgroundColor = vconf.backgroundColor ?? SSU_RED
	}

	setIndex(1, false)

	return div
}


const MY_CONFIG: ViewConfig[] = [
	KOLLEKTIVTRAFIK,
	BLI_MEDLEM,
	FOLJ_INSTA,
	GA_MED_I_S,
	MOTE_ONSDAG
]
